import React from 'react'
import { Nav,Tab } from 'react-bootstrap'
import Map from '../../../../Home/HomeTwo/Map/Map.HomeTwo.component'
import BannerHomeTen from "../../../../Home/HomeTen/Banner.HomeTen.component";
function BannerSingleV1() {
    return (
        <div className="single_page_listing_tab">
            <Tab.Container  defaultActiveKey="slider">
            <div class="container">
                <div class="row">
                    <div class="col-lg-12">
                        <Nav id="pills-tab1111" as="ul" className="nav nav-tabs">
                            <Nav.Item as="li">
                                <Nav.Link as="a"eventKey="slider"><span class="flaticon-photo-camera color-white"></span></Nav.Link>
                            </Nav.Item>
                            <Nav.Item as="li">
                                <Nav.Link as="a"eventKey="map"><span class="flaticon-pin color-white"></span></Nav.Link>
                            </Nav.Item>
                            <Nav.Item as="li">
                                <Nav.Link as="a"eventKey="streetview"><span class="flaticon-street-view color-white"></span></Nav.Link>
                            </Nav.Item>
                        </Nav>
                    </div>
                </div>
            </div>
                <Tab.Content id="pills-tabContent2" className="tab-content">
                    <Tab.Pane eventKey="slider">
                        <BannerHomeTen/>
                    </Tab.Pane>
                    <Tab.Pane eventKey="map">
                        <Map/>
                    </Tab.Pane>
                    <Tab.Pane eventKey="streetview">
                        <iframe className="h600 w100" src="https://www.google.com/maps/embed?pb=!4v1553797194458!6m8!1m7!1sR4K_5Z2wRHTk9el8KLTh9Q!2m2!1d36.82551718071267!2d-76.34864590837246!3f305.15097!4f0!5f0.7820865974627469"  allowFullScreen></iframe>
                    </Tab.Pane>
                </Tab.Content>
            </Tab.Container>
		</div>
    )
}

export default BannerSingleV1
