import React from 'react'
import {graphql, Link, useStaticQuery} from 'gatsby'
import Loadable from '@loadable/component'
const OwlCarousel = Loadable(() => import('react-owl-carousel3'))
const options = {
    loop: true,
    navContainerClass:"carousel-btn-block banner-carousel-btn",
    nav: true,
    dots: false,
    animateIn:"fadeIn",
    animateOut:'slideOutDown',
    autoplayHoverPause: false,
    autoplay: false,
    margin: 30,
    smartSpeed: 1200,
    // navElements:'<div className="carousel-btn-block banner-carousel-btn"></div>',
    navText: [
      	'<span class="carousel-btn left-btn"><i class="flaticon-left-arrow-1 left"></i></span>',
      	'<span class="carousel-btn right-btn"><i class="flaticon-right-arrow right"></i></span>'
    ],
    responsive: {
        0: {
            items: 1,
        },
        480:{
            items:1,
        },
        600: {
            items: 1,
        },
        768: {
            items: 1
        },
        992: {
            items: 1
        },
        1200: {
            items: 1
        },
        1280: {
            items: 1
        }
    }
}

const BannerHomeTen =()=>{
    const data = useStaticQuery(graphql`
	query{
		home1: file(relativePath: {eq: "home/7.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		home2: file(relativePath: {eq: "home/2.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		home3: file(relativePath: {eq: "home/1.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
	}`
)
    return(
        <div className="home10-mainslider">
		<div className="container-fluid p0">
			<div className="row">
				<div className="col-lg-12">
					<div className="main-banner-wrapper home10">
					    <OwlCarousel className="banner-style-one owl-theme owl-carousel" {...options}>
					        <div className="slide slide-one" style={{backgroundImage: `url(${data.home1.childImageSharp.fluid.originalImg})`, height:'620px'}}>
					            <div className="container">
					                <div className="row home-content">
					                    <div className="col-lg-12 text-center p0">
					                    	<h2 className="banner_top_title">$13,000<small>/mo</small></h2>
					                        <h3 className="banner-title">Gorgeous Villa Bay View</h3>
					                        <p>Beds: 3,  Baths: 4</p>
					                        <div className="btn"><Link to="#" className="banner-btn">Learn More</Link></div>
					                    </div>
					                </div>
					            </div>
					        </div>
					        <div className="slide slide-one" style={{backgroundImage: `url(${data.home2.childImageSharp.fluid.originalImg})`, height:'620px'}}>
					            <div className="container">
					                <div className="row home-content">
					                    <div className="col-lg-12 text-center p0">
					                    	<h2 className="banner_top_title">$13,000<small>/mo</small></h2>
					                        <h3 className="banner-title">Gorgeous Villa Bay View</h3>
					                        <p>Beds: 3,  Baths: 4</p>
					                        <div className="btn"><Link to="#" className="banner-btn">Learn More</Link></div>
					                    </div>
					                </div>
					            </div>
					        </div>
					        <div className="slide slide-one" style={{backgroundImage: `url(${data.home3.childImageSharp.fluid.originalImg})`, height:'620px'}}>
					            <div className="container">
					                <div className="row home-content">
					                    <div className="col-lg-12 text-center p0">
					                    	<h2 className="banner_top_title">$13,000<small>/mo</small></h2>
					                        <h3 className="banner-title">Gorgeous Villa Bay View</h3>
					                        <p>Beds: 3,  Baths: 4</p>
					                        <div className="btn"><Link to="#" className="banner-btn">Learn More</Link></div>
					                    </div>
					                </div>
					            </div>
					        </div>
					     </OwlCarousel>
                    </div>
				</div>
			</div>
		</div>
	</div>
    )
}
export default BannerHomeTen