import React, {useState} from 'react'
import { Link } from 'gatsby'
import SidebarComponents from '../../../../../Global/Pages/Listing/Single/SidebarComponents'
import AdvanceSearchWidgetSidebar from '../../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'
import SingleListingSection from '../../../../../Global/Pages/Listing/Single/SingleListingSection.component'

function SingleV1Section() {
	const [SideBarShow, setSideBarShow] = useState(false);
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
            document.querySelector('body').classList.toggle('body_overlay')
            document.querySelector('.sidebar_content_details').classList.toggle('sidebar_ml0')
    }
    return (
        <section className="our-agent-single bgc-f7 pb30-991">
		<div className="container">
			<div class="row">
				<div class="col-lg-12">
					<div class="listing_sidebar dn db-991">
						<div class="sidebar_content_details style3">
							<div class="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
								<div class="sidebar_advanced_search_widget">
									<h4 class="mb25">
                                    Advanced Search <Link class="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span class="flaticon-close"></span>
                                    </Link>
                                    </h4>
									<AdvanceSearchWidgetSidebar index="1" style2/>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
          	<div className="row">
                <div className="col-lg-7 col-xl-8">
					<div className="dn db-991 mt30 mb0">
						<div id="main2">
							<span id="open2" className="flaticon-filter-results-button filter_open_btn style3" onClick={handleSidebar}> Show Filter</span>
						</div>
					</div>
					<div className="single_property_title mt30-767 mb30-767">
						<h2>Luxury Family Home</h2>
						<p>1421 San Pedro St, Los Angeles, CA 90015</p>
					</div>
				</div>
				<div className="col-lg-5 col-xl-4">
					<div className="single_property_social_share">
						<div className="price float-left fn-400">
							<h2>$13,000<small>/mo</small></h2>
						</div>
						<div className="spss mt20 text-right tal-400">
							<ul className="mb0">
								<li className="list-inline-item"><a href="#"><span className="flaticon-transfer-1"></span></a></li>
								<li className="list-inline-item"><a href="#"><span className="flaticon-heart"></span></a></li>
								<li className="list-inline-item"><a href="#"><span className="flaticon-share"></span></a></li>
								<li className="list-inline-item"><a href="#"><span className="flaticon-printer"></span></a></li>
							</ul>
						</div>
					</div>
				</div>
                <div className="col-md-12 col-lg-8 mt50">
                    <SingleListingSection/>
                </div>
				<div  class="col-lg-4 col-xl-4 mt50">
                	<SidebarComponents/>
				</div>
            </div>  
        </div>
        </section>
       
    )
}

export default SingleV1Section
