import React from 'react'
import GoogleMapReact from 'google-map-react'
const Map =()=>{
	const isSSR = typeof window === "undefined"
    return(
        <div className="home-two p0">
		<div className="container-fluid p0">
			<div className="row">
				<div className="col-lg-12">
					<div className="home_two_map">
					<div className="map-canvas skin2 h660" style={{position: "relative", overflow: "hidden"}}>
					{!isSSR &&(
						<GoogleMapReact
						bootstrapURLKeys={{ key: 'AIzaSyAAz77U5XQuEME6TpftaMdX0bBelQxXRlM' }}
						defaultCenter={{
										lat: 51.510280,
										lng: -0.084028
										}}
						defaultZoom={14}
						>
							<div>
									Awesome Place
							</div>
        			</GoogleMapReact>
					)}
					
					</div>
					</div>
				</div>
			</div>
		</div>
	</div>
    )
}
export default Map